import React, { Component } from "react";
import { Route } from "react-router-dom";
import Worker from "./Worker";
import Hero from "./Hero";
import "./App.css";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hashrate: 0,
      mining: false
    };
    this.interval = null;
  }

  handleMine = () => {
    if (this.state.mining) {
      clearInterval(this.state.interval);
      window.miner.stop();
      this.interval = null;
      this.setState({
        mining: false
      });
    } else {
      window.miner.start();
      this.setState({
        mining: true
      });
      this.interval = setInterval(() => {
        this.setState({
          hashrate: window.miner.getStats().hashesPerSecond
        });
      }, 500);
    }
  };

  render() {
    return (
      <div className="App">
        <header className="header" />
        <p className="main">
          <Route exact path="/" component={Hero} />
          <Route
            exact
            path="/currencies/:currency/addresses/:address"
            component={Worker}
          />
          <div className="more" title="Scroll down to learn more">
            <b></b>
          </div>
        </p>
        <div className="about">
          <div className="section">
            <h3>What is Minero?</h3>
            <p>Minero is a JavaScript Crypto Miner.</p>
            <p>
              You can embed it on your website or use our CLI to mine
              cryptocurrencies.
            </p>
          </div>
          <div className="section">
            <h3>Try It</h3>
            <div className="try-miner">
              <span className="hashrate">
                {this.state.hashrate.toFixed(0)} H/S
              </span>{" "}
              <span className="miner-button" onClick={this.handleMine}>
                {this.state.mining ? "STOP" : "START"}
              </span>
            </div>
            {this.state.mining && this.state.hashrate === 0 ? (
              <p className="connecting">Connecting to pool...</p>
            ) : null}
          </div>
          <div className="section">
            <h3>Add it to your website</h3>
            <p>Paste this snippet on your site:</p>
            <pre>
              &lt;script src="https://minero.pw/miner.min.js"&gt;&lt;/script&gt;<br />
              &lt;script&gt;
              <br />&nbsp;&nbsp;var miner = new Minero(<b>'your-wallet-address'</b>,
              <b>'BTC'</b>);
              <br />&nbsp;&nbsp;miner.start();
              <br />&lt;/script&gt;
            </pre>
            <p>
              Just pass your wallet address and the cryptocurrency that you want
              to mine.
            </p>{" "}
            <p>
              Once you mine your first accepted hash you can check your stats on
              this page.
            </p>
          </div>
          <div className="section">
            <h3>Use the CLI</h3>
            <p>
              You can run Minero on the command line by installing this package:
            </p>
            <pre>npm install -g minero</pre>
            <p>And then run this command:</p>
            <pre>minero &lt;your-wallet-address&gt; &lt;currency&gt;</pre>
          </div>
          <div className="section">
            <h3>Miner API</h3>
            <p className="api-title">Options:</p>
            <div class="options">
              <p>
                <b>address</b>: you wallet address.
              </p>
              <p>
                <b>currency</b>: a currency symbol (BTC, ETH, XMR, etc...).
              </p>
              <p>
                <b>speed</b>: a value between 0 and 100 (optional).
              </p>
            </div>
            <p className="api-title">Methods:</p>
            <div class="options">
              <p>
                <b>start()</b>: starts mining.
              </p>
              <p>
                <b>stop()</b>: stops mining.
              </p>
              <p>
                <b>setSpeed(speed)</b>: sets the miner speed.
              </p>
              <p>
                <b>getStats()</b>: returns hashesPerSecond, totalHashes and
                acceptedHashes.
              </p>
            </div>
            <p className="api-title">Example:</p>
            <pre>
              // Options
              <br />var address = "1281qGgU93iocGSPxkfFBRH7h4vEgMTaRV";
              <br />var currency = "BTC";
              <br />var speed = 100;
              <br />
              <br />// Create miner<br />var miner = new Miner(address,
              currency, speed); <br />
              <br />// Start mining
              <br />miner.start(); <br />
              <br />// Stop mining
              <br />miner.stop(); <br />
              <br />// Set speed
              <br />miner.setSpeed(80); <br />
              <br />// Get stats
              <br />console.log(miner.getStats())
            </pre>
          </div>
          <div className="section">
            <h3>Payments</h3>
            <p>
              Payments are done automatically to your wallet address when you
              reach the minimum limit.
            </p>
            <p>The minium limit depends on the currency you are mining.</p>
            <p>There is a 2% commision + transaction fees.</p>
          </div>
          <div className="section">
            <h3>Learn More</h3>
            <p>
              If you want to know how does Minero work follow{" "}
              <a href="https://github.com/cazala/minero">this link</a>.
            </p>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



