import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./Hero.css";

import apis from "./apis";

class Hero extends Component {
  constructor(props) {
    super(props);
    this.api = apis[(Math.random() * apis.length) | 0];
    this.state = {
      loading: false,
      currency: localStorage.currency || "BTC",
      address: localStorage.address || "",
      coins: localStorage.coins ? JSON.parse(localStorage.coins) : null,
      error: null,
      changing: false
    };
  }

  componentWillMount() {
    this.setState({
      loading: this.state.coins == null
    });
    fetch(`${this.api}/coins`)
      .then(resp => resp.json())
      .then(json => {
        if (json.success) {
          this.setState({
            loading: false,
            coins: json.data
          });
          localStorage.coins = JSON.stringify(json.data);
        } else {
          this.setState({
            loading: false,
            error: json.error
          });
        }
      })
      .catch(e =>
        this.setState({
          loading: false,
          error: e.message
        })
      );
  }

  render() {
    if (this.state.loading) {
      return <p className="hero-loading">Loading...</p>;
    }
    if (this.state.error) {
      return <p className="hero-error">${this.state.error}</p>;
    }
    const coin = this.state.coins.find(x => x.symbol === this.state.currency);
    const disabled = this.state.address.length === 0;
    const view =
      this.state.changing == true ? (
        <select
          value={this.state.currency}
          onChange={e => {
            this.setState({ currency: e.target.value, changing: false });
            localStorage.currency = e.target.value;
          }}
        >
          {this.state.coins.map(coin => {
            return <option value={coin.symbol}>{coin.name}</option>;
          })}
        </select>
      ) : (
        <p className="hero-currency">
          {coin.name}
          <a onClick={() => this.setState({ changing: true })}>(change)</a>
        </p>
      );
    return (
      <div className="hero">
        <img
          src={coin.image}
          onClick={() => this.setState({ changing: true })}
        />
        <div>{view}</div>
        <div>
          <input
            tabIndex={disabled ? 1 : -1}
            placeholder={`Enter your ${coin.name} address...`}
            value={this.state.address}
            onChange={e => {
              this.setState({
                address: e.target.value
              });
              localStorage.address = e.target.value;
            }}
          />
          <Link
            to={
              disabled
                ? "/"
                : `/currencies/${this.state.currency}/addresses/${this.state
                    .address}`
            }
          >
            <div
              tabIndex={disabled ? -1 : 1}
              className="access"
              title={
                disabled
                  ? `You need to enter your ${coin.name} wallet address to access`
                  : void 0
              }
              disabled={disabled}
            >
              Access
            </div>
          </Link>
        </div>
        <p className="info">Private, secure mining. No account needed.</p>
      </div>
    );
  }
}

export default Hero;



