import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./Worker.css";
import apis from "./apis";
import moment from "moment";

function format(hashes) {
  if (hashes > 1000000000000) {
    return (hashes / 1000000000000).toFixed(2) + " T";
  }
  if (hashes > 1000000000) {
    return (hashes / 1000000000).toFixed(2) + " G";
  }
  if (hashes > 1000000) {
    return (hashes / 1000000).toFixed(2) + " M";
  }
  if (hashes > 1000) {
    return (hashes / 1000).toFixed(2) + " K";
  }
  return hashes + " ";
}

class Worker extends Component {
  constructor(props) {
    super(props);
    this.api = apis[(Math.random() * apis.length) | 0];
    this.state = {
      data: null,
      loading: false,
      error: null
    };
  }

  componentWillMount() {
    this.setState({
      loading: true
    });
    fetch(
      `${this.api}/currencies/${this.props.match.params
        .currency}/addresses/${this.props.match.params.address}`
    )
      .then(resp => resp.json())
      .then(json => {
        if (json.success) {
          this.setState({
            loading: false,
            data: json.data
          });
        } else {
          this.setState({
            loading: false,
            error: json.error
          });
        }
      })
      .catch(e =>
        this.setState({
          loading: false,
          error: e.message
        })
      );
  }

  render() {
    if (this.state.loading) {
      return <p>Loading...</p>;
    }

    const exit = (
      <Link to="/">
        <div className="exit">Exit</div>
      </Link>
    );

    if (this.state.error) {
      if (this.state.error === "Not Found") {
        return (
          <div>
            <div className="error">
              <p>Not Found</p>
              <span className="info">
                You need to mine at least 1 accepted hash in order to show up
                here.
              </span>
            </div>
            {exit}
          </div>
        );
      } else {
        return (
          <div>
            <div className="error">
              <p>{this.state.error}</p>
            </div>
            {exit}
          </div>
        );
      }
    }

    return (
      <div className="worker">
        <div>
          <img src={this.state.data.coin.image} />
          <p className="currency">{this.state.data.coin.name}</p>
        </div>
        <div className="address">
          Address: <span>{this.state.data.address}</span>
        </div>
        <div>
          <div className="third">
            <div>HASHRATE</div>
            <p>{format(this.state.data.rate)}H/S</p>
          </div>
          <div className="third">
            <div>TOTAL HASHES</div>
            <p>{format(this.state.data.hashes).replace(".00", "")}</p>
          </div>
          <div className="third">
            <div>BALANCE</div>
            <p>
              {this.state.data.balance.unconfirmed.toFixed(8)}{" "}
              {this.state.data.currency}
            </p>
          </div>
        </div>
        <div className="pool-info">
          <div className="column left">
            <span className="row">
              <p>Block reward:</p> {this.state.data.info.reward.toFixed(5)}{" "}
              {this.state.data.currency}
            </span>
            <span className="row">
              <p>Difficulty:</p> {format(this.state.data.info.difficulty)}
            </span>
          </div>
          <div className="column right">
            <span className="row">
              <p>Minimum payment:</p> {this.state.data.info.minimum.toFixed(
                5
              )}{" "}
              {this.state.data.currency}
            </span>
            <span className="row">
              <p>Payout:</p> 98%
            </span>
          </div>
          <span className="row important">
            You get payed <b>
              {this.state.data.info.rewardPerG.toFixed(5)}
            </b>{" "}
            {this.state.data.currency} for every <b>1G</b> hashes
          </span>
        </div>
        {exit}
      </div>
    );
  }
}

export default Worker;



